<template>
  <div class="h-100">
    <div ref="pageLoaderContainer">
      <CubeLoader :can-show="showLoader"></CubeLoader>
    </div>
    <header>
      <HeaderComponent></HeaderComponent>
    </header>

    <div class="container-fluid p-0 h-100">
      <router-view v-slot="{ Component }">
        <transition @enter="onEnter" @leave="onLeave" :css="false" mode="out-in">
          <component :is="Component" />
        </transition>
      </router-view>
    </div>
  </div>
</template>


<script setup>
import "bootstrap/dist/css/bootstrap.min.css"
import "@/assets/css/_global_styles.scss"

import HeaderComponent from './components/app-ui/HeaderComponent.vue'
import CubeLoader from './components/app-ui/CubeLoader.vue'

import { ref, computed, onMounted, onUnmounted } from "vue";
import { transitionEnter, transitionLeave } from "@/utils/pageTransition";

const showLoader = ref(true);
const pageLoaderContainer = ref(null);

const bodyHeight = ref(document.documentElement.clientHeight);

const valideHeight = computed(() => {
  return bodyHeight.value + "px";
});

function onEnter() {
  transitionEnter(pageLoaderContainer.value, () => {
    showLoader.value = false
  });
}

function onLeave(el, done) {
  showLoader.value = true;
  transitionLeave(pageLoaderContainer.value, done);
}


function onWindowResized() {
  bodyHeight.value = document.documentElement.clientHeight;
  document.body.style.height = valideHeight.value;
}

onMounted(() => {
  onWindowResized();
  window.addEventListener('resize', onWindowResized);
});

onUnmounted(() => {
  window.removeEventListener('resize', onWindowResized);
});

</script>


