<template>
    <div class="filter position-absolute top-50 start-50 translate-middle">
        <div class="d-flex justify-content-center">

            <div id="filter" class="toast" role="alert" aria-live="assertive" aria-atomic="true"
                data-bs-autohide="false" ref="filter">
                <div class="toast-header bg-dark text-white">
                    <strong class="me-auto">Filtrage de Lot</strong>
                    <button type="button" class="btn-close btn-close-white" @click="closeToast"></button>
                </div>
                <div class="toast-body">

                    <div class="slider">
                        <div class="fs-6 text-secondary text-center ">
                            <span>Surface en M²</span>
                        </div>

                        <div class="position-relative ranges">
                            <input type="range" class="form-range position-absolute" id="min-range" ref="minSlider"
                                :max="max" :min="min" :step="step" v-model="minValue" @input="controlSlider(true)">
                            <input type="range" class="form-range position-absolute" id="max-range" ref="maxSlider"
                                :max="max" :min="min" :step="step" v-model="maxValue" @input="controlSlider(false)">
                        </div>
                    </div>

                    <div class="range-info d-flex justify-content-between align-items-center my-4">

                        <div class="form-floating mx-4">
                            <input type="number" id="min-input" class="form-control" :value="minValue"
                                @input="debounceMin">
                            <label for="min-input">min</label>
                        </div>

                        <div class="form-floating mx-4">
                            <input class="form-control" type="number" id="max-input" :value="maxValue"
                                @input="debounceMax">
                            <label for="max-input">max</label>
                        </div>

                    </div>

                    <hr />


                    <div class="types">

                        <div class="fs-6 text-center text-secondary mb-3">
                            <span>Types d'appartement</span>
                        </div>

                        <div class="d-flex justify-content-center">

                            <div class="me-2 w-50" v-for="(value, key, index) in colorsTypes" :key="index">
                                <input type="checkbox" class="btn-check" :id="'checkbox-' + index" :value="key"
                                    v-model="checkedTypes" autocomplete="off">
                                <label class="btn border-0 text-white text-start w-100" :for="'checkbox-' + index"
                                    :style="{ 'background-color': value }">
                                    {{ key }}
                                </label>
                            </div>


                        </div>

                    </div>
                </div>
            </div>
        </div>
    </div>

</template>


<script setup>

import { Toast } from "bootstrap"
import debounce from "lodash/debounce"
import { ref, reactive, watch, onMounted, onUnmounted } from "vue";


const emits = defineEmits([
    "onCloseToast",
    "onSurfaceChanged",
    "onTypesChanged"
]);


const props = defineProps({
    showToast: {
        type: Boolean,
        default: true
    },
    min: {
        type: Number,
        default: 0
    },
    max: {
        type: Number,
        default: 100
    },
    step: {
        type: Number,
        default: 1
    },
    gap: {
        type: Number,
        default: 10
    },
    sliderColor: {
        type: String,
        default: "#cecece"
    },
    rangeColor: {
        type: String,
        default: "#21af4f"
    },
    colorsTypes: Object
});


const toast = ref(null),
    appReady = ref(false),
    minValue = ref(props.min),
    maxValue = ref(props.max),
    surface = reactive({
        min: minValue,
        max: maxValue,
    }),
    checkedTypes = ref([]);

const maxSlider = ref(null);
const filter = ref(null);

function initSliders() {
    fillSlider(minValue.value, maxValue.value);
}

function initToast() {
    toast.value = new Toast(filter.value);
    toogleToast();
}
function toogleToast() {
    if (props.showToast) {
        toast.value.show();
    } else {
        toast.value.hide();
    }
}

function closeToast() {
    emits("onCloseToast");
}


function controlSlider(isMinSlider) {
    const gap = Number(props.gap);
    const min = Number(minValue.value);
    const max = Number(maxValue.value);
    const validMax = max - gap;

    if (min < props.min) {
        minValue.value = props.min;
        fillSlider(min, max);
        return;
    }

    if (max > props.max) {
        maxValue.value = props.max;
        fillSlider(min, max);
        return;
    }

    if (isMinSlider) {
        if (min > validMax) {
            minValue.value = validMax;
            surface.min = validMax;
            fillSlider(validMax, max,);
            return;
        }
        fillSlider(min, max);
        surface.min = min;
        return;
    }

    if (minValue.value > validMax) {
        maxValue.value = surface.max = min + gap;
        fillSlider(min, maxValue.value);
        return;
    }
    surface.max = max;
    fillSlider(min, max);

}

function fillSlider(from, to) {
    const rangeDistance = props.max - props.min;
    const fromPosition = from - props.min;
    const toPosition = to - props.min;
    const sliderColor = props.sliderColor;
    const rangeColor = props.rangeColor;

    maxSlider.value.style.background = `linear-gradient(
            to right,
            ${sliderColor} 0%,
            ${sliderColor} ${(fromPosition) / (rangeDistance) * 100}%,
            ${rangeColor} ${((fromPosition) / (rangeDistance)) * 100}%,
            ${rangeColor} ${(toPosition) / (rangeDistance) * 100}%, 
            ${sliderColor} ${(toPosition) / (rangeDistance) * 100}%, 
            ${sliderColor} 100%)`;
}

function surfaceChanged() {

    if (appReady.value) {
        emits("onSurfaceChanged", surface);
    }
}

function typesChanged() {
    if (appReady.value) {
        emits("onTypesChanged", checkedTypes.value);
    }
}

const debounceMin = debounce((evt) => {
    minValue.value = evt.target.value;
    controlSlider(true)
}, 1000);

const debounceMax = debounce((evt) => {
    maxValue.value = evt.target.value;
    controlSlider(false);
}, 1000);


function _appReady() {
    appReady.value = true;
    //checkedTypes.value = Object.keys(props.colorsTypes); // setting checked state for all types by default 
    surfaceChanged();
}




watch(() => props.showToast, () => {
    toogleToast();
});
watch(surface, () => {
    surfaceChanged();
});
watch(checkedTypes, () => {
    typesChanged();
});


onMounted(() => {
    initToast();
    initSliders();
    window.addEventListener("OnAppReady", _appReady)
});

onUnmounted(() => {
    window.removeEventListener("OnAppReady", _appReady)
});

</script>



<style lang="scss" scoped>
.filter {
    z-index: 1031;
    max-width: 60%;
    width: auto;

    .toast-header {
        letter-spacing: .13em;
        font-size: .9em;
        font-weight: 500;
    }
}

.ranges {
    margin: 1em 0;
    min-height: 1em;
}

input[type=range] {
    height: 0.3em;
    width: 100%;
    position: absolute;
    background-color: #C6C6C6;
    pointer-events: none;
    border-radius: 2em;
}

input[type=range]::-webkit-slider-thumb {
    cursor: pointer;
    pointer-events: all;
}

input[type=range]::-webkit-slider-runnable-track {
    background-color: transparent;

}

#min-range {
    top: 0.15em;
    height: 0;
    z-index: 1;
}

.btn-check {
    +.btn {
        letter-spacing: .2em;
        font-size: .9em;
        font-weight: 600;
        border-radius: .2em;
    }

}

.btn-check:checked {
    +.btn {
        filter: brightness(0.9);
        position: relative;

        &:after {
            position: absolute;
            right: .5em;
            margin-right: 0.3em;
            text-align: center;
            content: 'L';
            color: #ffffff;
            transform: scaleX(-1) rotate(-35deg);
        }
    }

}
</style>