<template>
    <div class="status-parent">
        <div class="fs-6 text-center text-secondary mb-3">
            <span>Status</span>
        </div>

        <div class="d-flex justify-content-center text-white">
            <div class="me-2">
                <input type="checkbox" class="btn-check" id="dispo" v-model="status" value="Disponible" autocomplete="off">
                <label class="btn custom-checkbox border-0 text-white text-start bg-success pe-4" for="dispo">
                    Dispo
                </label>
            </div>

            <div class="me-2">
                <input type="checkbox" class="btn-check" id="indispo" v-model="status" value="Indisponible"
                    autocomplete="off">
                <label class="btn custom-checkbox border-0 text-white text-start bg-danger pe-4" for="indispo">
                    Vendu
                </label>
            </div>

        </div>
    </div>
</template>


<script setup>

import { ref, watch, onMounted } from "vue";

const emits = defineEmits([
    "onStatusChanged"
]);

const status = ref(["Disponible"]);

function statusChanged() {
    emits("onStatusChanged", status.value);
}

watch(status, () => {
    statusChanged();
});

onMounted(() => {
    statusChanged()
})
</script>


