<template>
    <img :src="getImagePath()" class="img-fluid" alt="" loading="lazy">
</template>


<script setup>


const props = defineProps({
    src: {
        type: String,
        default: ""
    },
});

const defaultImage = "https://via.placeholder.com/512x256.png/fff/ccc?text=Image not found";

function getImagePath() {
    let src = "";
    try {
        src = require(`@/${props.src}`);
    } catch (error) {
        src = defaultImage;
    }
    return src;
}


</script>

<style lang="scss" scoped></style>